import React, { useCallback, useMemo, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDropzone } from "react-dropzone";

import { Figure } from "react-bootstrap";

const baseStyle = {
  flex: 1,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  padding: "20px",
  borderWidth: 2,
  borderRadius: 2,
  borderColor: "#eeeeee",
  borderStyle: "dashed",
  backgroundColor: "#fafafa",
  color: "#bdbdbd",
  outline: "none",
  transition: "border .24s ease-in-out",
};

const activeStyle = {
  borderColor: "#2196f3",
};

const acceptStyle = {
  borderColor: "#00e676",
};

const rejectStyle = {
  borderColor: "#ff1744",
};

/**
 * App: Dropzone
 */
export default (args) => {
  // console.log("DROPZONE", args);
  /**
   * Default Properties
   */
  let defaults = {};

  /**
   * Properties
   */
  const props = Object.assign(defaults, args.data);

  /**
   * Barrels
   */
  let { accountPhotoPath } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Hooks
   */
  const [, setFiles] = useState(null);

  /**
   * Custom hooks
   */
  const { t } = useTranslation("AccountRegistration");

  // let setFieldValue = data.setFieldValue;
  // let data = args.data;

  // let accountPhotoPath = data.accountPhotoPath;
  if (accountPhotoPath !== "") {
    baseStyle.padding = 0;
    baseStyle.borderStyle = "none"; // "solid";
    baseStyle.backgroundColor = "none";
  }

  // const [thisData] = useState(data);
  // console.log("THIS DATA =====");

  // console.log(data);

  const onDrop = useCallback((acceptedFiles) => {
    // console.log(getRootProps());

    // args.onDrop(1);
    // args.context.dropzoneUntouched = false;

    setFiles(acceptedFiles);
    // sendRequest({
    //   req: "upload file",
    //   data: { acceptedFiles: acceptedFiles },
    // });
    let data = { acceptedFiles: acceptedFiles };
    sendRequest({ req: "upload file", data: data });
    // setFieldValue(1);
    // eslint-disable-next-line
  }, []);

  const {
    // acceptedFilesItems,
    // rejectedFilesItems,
    getRootProps,
    getInputProps,
    isDragActive,
    isDragAccept,
    isDragReject,
  } = useDropzone({
    accept: "image/*",
    onDrop,
  });

  // const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });
  // const { getRootProps, getInputProps, isDragActive } = useDropzone({
  //   onDrop
  // });

  const style = useMemo(
    () => ({
      ...baseStyle,
      ...(isDragActive ? activeStyle : {}),
      ...(isDragAccept ? acceptStyle : {}),
      ...(isDragReject ? rejectStyle : {}),
    }),
    [isDragActive, isDragAccept, isDragReject]
  );

  if (accountPhotoPath === "") {
    return (
      <div className="container">
        <div {...getRootProps({ style })}>
          <input {...getInputProps()} />
          <p>{t("Click to select the image file")}</p>
        </div>
      </div>
    );
  } else {
    // accountPhotoPath = "http://localhost:9000/" + accountPhotoPath;
    accountPhotoPath = "gfx/photo_id.png";

    return (
      <>
        <div className="container">
          <div {...getRootProps({ style })}>
            <input {...getInputProps()} />
            <Figure.Image
              width={171}
              height={180}
              alt="171x180"
              src={accountPhotoPath}
            />
          </div>
        </div>
      </>
    );
  }
};
